import alt from "../alt";
import {getStars} from "../requests";

class StarListActions {
  constructor() {
    this.generateActions("getStarListDone", "getStarListFail");
  }
  getStarList(query) {
    return (dispatch) => {
      dispatch(query);
      getStars
        .fail((jqXhr) => this.getStarListFail({ status: "danger", message: "Could not load data." }))
        .done((data) => {
          var result = query ? data.filter(star => star.name.join().match(new RegExp(query, "i"))) : data;
          this.getStarListDone(result);
        });
    };
  }
}

export default alt.createActions(StarListActions);
