import alt from "../alt";
import {getStars} from "../requests";

class StarActions {
  constructor() {
    this.generateActions("getStarDone", "getStarFail");
  }
  getStar(name) {
    return (dispatch) => {
      dispatch();
      getStars
        .fail((jqXhr) => this.getStarFail({ status: "danger", message: "Could not load data." }))
        .done((data) => {
          var result = data.find(star => star.name.some(d => d == name));
          if (result)
            this.getStarDone(result);
          else
            this.getStarFail({ status: "danger", message: "This star was not found." });
        });
    };
  }
}

export default alt.createActions(StarActions);
