import alt from "../alt";
import StarActions from "../actions/StarListActions";

class StarListStore {
  constructor() {
    this.bindListeners({
      getStarList: StarActions.GET_STAR_LIST,
      getStarListDone: StarActions.GET_STAR_LIST_DONE,
      getStarListFail: StarActions.GET_STAR_LIST_FAIL
    });
    this.query = null;
    this.data = [];
    this.status = null;
    this.message = null;
  }
  getStarList(payload) {
    this.query = payload;
    this.data = [];
  }
  getStarListDone(payload) {
    this.status = null;
    this.message = null;
    this.data = payload.map(d => {

      if (d.orbit.length > 0)
        var distance = d.orbit[0].a * (1 - d.orbit[0].e);
      else if (d.proper_motion.length > 0)
        var distance = Math.sqrt(Math.pow(d.proper_motion[0].x0, 2) + Math.pow(d.proper_motion[0].y0, 2));

      return {
        id: d.id,
        name: d.name,
        display_name: d.name.join(" / "),
        magnitude: d.magnitude.find(d => d.band == "K").value,
        distance: distance,
        N_orbit: d.orbit.length,
        N_proper_motion: d.proper_motion.length
      }});
  }
  getStarListFail(error) {
    this.status = error.status;
    this.message = error.message;
  }
}

export default alt.createStore(StarListStore);
